import React from 'react';
import styled from 'styled-components';
import Section from './Section';
import models from '../model-s.jpg';
import modelY from '../model-y.jpg';
import model3 from '../model-3.jpg';
import modelX from '../model-x.jpg';
import solarPanel from '../solar-panel.jpg';
import solarRoof from '../solar-roof.jpg';
import Accessories from '../accessories.jpg';

function Home() {
    return (
        <Container> 
            <Section 
                title="Model S"
                description="Order Online for Touchless Delivery"
                backgroungImg= { models }
                LeftBtnText="Custom Order"
                RightBtnText="Existing Inventory"
            />
            <  Section 
                title="Model Y"
                description="Order Online for Touchless Delivery"
                backgroungImg={modelY}
                LeftBtnText="Custom Order"
                RightBtnText="Existing Inventory"
            />
            <Section 
                title="Model 3"
                description="Order Online for Touchless Delivery"
                backgroungImg={model3}
                LeftBtnText="Custom Order"
                RightBtnText="Existing Inventory"
            />
            <Section 
                title="Model X"
                description="Order Online for Touchless Delivery"
                backgroungImg={modelX}
                LeftBtnText="Custom Order"
                RightBtnText="Existing Inventory"
            />
            
            <Section 
                title="Lowest Cost Solar Panels in America"
                description="Money-back guarantee"
                backgroungImg={ solarPanel }
                LeftBtnText="Order now"
                RightBtnText="Learn More"
            />
            <Section 
                title="Solar for New Roofs"
                description="Solar Roof Costs Less Than a New Roof Plus Solar Panels"
                backgroungImg={solarRoof}
                LeftBtnText="Order now"
                RightBtnText="Learn More"
            />
            <Section 
                title="Accessories"
                description=""
                backgroungImg={ Accessories }
                LeftBtnText="Shop now"
            />
        </Container>
    )
}

export default Home;

const Container = styled.div`
    height: 100vh; 
`
